@use 'base/mixins' as m;
@use 'base/variables' as v;
.crear{
    margin-top: 5rem;
    .uptask{
    @include m.textoDegradado(v.$rosa);
    }
    input[type="submit"]{
        @include m.boton(v.$rosa, v.$blanco)
    }
    .acciones{
        a{
            color: lighten(v.$rosa, 20%);
        }
    }
    input{
        background-color: lighten(v.$rosa, 40%);
    }
}