@use 'base/variables' as v;
@use 'base/mixins' as m;
.barra-mobile{
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.tablet {
        display: none;        
    }
    h1{
        color: v.$blanco;
        margin: 0;
    }
    .menu{
        img{
            width: 6.5rem;
            &:hover{
                cursor: pointer;
            }
        }
    }
}
.barra{
    display: none;
    @include m.tablet {
        display: block;
    }
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;      
    }
    p{
        margin: 0;
        font-size: 1.4rem;
        span{
            font-weight: 700;
            color: v.$indigo;
        }
    }
    .cerrar-sesion{
        @include m.boton(v.$indigo, v.$blanco);
        border-radius: 1rem;
        font-size: 1.4rem;
    }
}