@use 'base/mixins' as m;
@use 'base/variables' as v;
.login{
    margin-top: 5rem;
    .uptask{
        @include m.textoDegradado(v.$cyan);
    }
    input[type="submit"]{
        @include m.boton(v.$cyan, v.$blanco)
    }
    .acciones{
        a{
            color: lighten(v.$cyan, 20%);
        }
    }
    input{
        background-color: lighten(v.$cyan, 50%);
    }
}