@use 'base/mixins' as m;
@use 'base/variables' as v;
.reestablecer{
    margin-top: 5rem;
    .uptask{
    @include m.textoDegradado(v.$indigo);
    }
    input[type="submit"]{
        @include m.boton(v.$indigo, v.$blanco)
    }
    .acciones{
        a{
            color: lighten(v.$indigo, 20%);
        }
    }
    input{
        background-color: lighten(v.$indigo, 40%);
    }
}